<template>
  <div class="parent">
    <div class="top">
      <SearchBar :show_date="false"/>
    </div><br>
    <div>
    	<h2 style="text-align:center;">All transactions</h2>
    </div><br>
    <div class="recycler">
    </div>
		<div class="scrollable-tab">
        <table class="table paiements">
          <thead>
            <tr class="panier-item">
              <th>id</th>
              <th>nom</th>
              <th>prenom</th>
              <th>montant</th>
              <th>date</th>
              <th>valide</th>
              <th>
              	<button @click="add_mode=true">Ajouter</button>
              </th>
            </tr>
          </thead>
          <tbody id="paiements">
						<tr v-for="item, count in items">
              <td>{{ count+1 }}</td>
              <td>{{ item.nom }}</td>
              <td>{{ item.prenom }}</td>
              <td>{{money(item.montant)}} Fbu</td>
              <td>{{ item.validated }}</td>
              <td>{{datetime(item.date)}}</td>
              <td>
                <div class="btns">
                  <button @click.prevent="startEdit(item)">Edit</button>
                  <button @click.prevent="startAchat(item)">valider</button>
                </div>
              </td>
            </tr>
        </tbody>
		<tfoot>
			<tr class="panier-item">
				<th colspan="3">total</th>
				<th>{{money(total())}} Fbu</th>
				<th></th>
				<th></th>
				<th></th>
			</tr>
		</tfoot>
      </table>
	</div>
  	<EditDialog :visible="edit_mode" :produit="active_item" @close="exitEdition"/>
  	<AchatDialog :visible="achat_mode" :produit="active_item" @close="exitEdition"/>
  	<ProduitDialog :visible="add_mode" @close="exitEdition"/>
  </div>
</template>
<script>
import axios from "axios"
import SearchBar from "../components/searchbar.vue";
import Item from "../components/achat_item.vue";
import EditDialog from "../components/dialog_edit_produit.vue";
import ProduitDialog from "../components/dialog_produit.vue";
import AchatDialog from "../components/dialog_achat.vue";

export default{
	components:{SearchBar, Item, EditDialog, AchatDialog, ProduitDialog},
	data(){
		return {
			csvData : {},
			items:this.$store.state.transactions,
			active_item:{},
			edit_mode:false, achat_mode:false, add_mode:false
		}
	},
	watch:{
		"$store.state.transactions"(new_val){
			this.items = new_val;
		}
	},
	methods:{
		fetchData(){
      let headers = {
          headers: {
            "Authorization": "Bearer " + this.$store.state.user.access
          }
        }
      if(this.$store.state.users.length==0){
        axios.get(this.$store.state.url+'/transaction/', headers)
        .then((response) => {
          this.$store.state.transactions = response.data.results;
          this.items = response.data.results;
          console.log(response.data.results)
        }).catch((error) => {
          if(error.response){
          }else{
            console.error(error)
          }
        });
      }
    },
		total(){
			let total = 0;
			for(let item of this.items){
				total += (item.montant);
			}
			return total;
		},
		exitEdition(){
			this.active_item = {};
			this.edit_mode = false;
			this.achat_mode = false;
			this.add_mode = false;
		},
		startEdit(item){
			this.active_item = item;
			this.edit_mode = true;
		},
		startAchat(item){
			this.active_item = item;
			this.achat_mode = true;
		},

		download (data){
			const blob = new Blob([data], {type: 'text/csv'});
			const url = window.URL.createObjectURL(blob);
			const a = document.createElement('a');
			a.setAttribute('hidden', '');
			a.setAttribute('href', url);
			a.setAttribute('download', 'model.csv');
			document.body.appendChild(a);
			a.click();
			document.body.removeChild(a);
        },
		level(quantite){
			if(quantite==0) return "empty";
			if(quantite<=5) return "low";
		},
		onPickFile () {
		  this.$refs.fileInput.click();
		},
	},
	mounted(){
		this.fetchData()
	}
};
</script>
<style scoped>
.top{
  display: flex;
  justify-content: center;
}
.empty{
	color: red;
}
.low{
	color:#a50;
}
.scrollable-tab{
  width: 100%;
  /*height: 0px;*/
  overflow: auto;
  padding: 0 20px;
  position: relative;
}
.scrollable-tab table thead tr th{
  position: sticky;
  top: 0;
}
.scrollable-tab table tfoot tr th{
  position: sticky;
  bottom: 0;
}
.btns{
	display: flex;
}
.hidden{
  visibility: hidden;
}
@media screen and (max-width: 650px){
  .scrollable-tab{
    position: inherit;
    height: auto;
    overflow-y: hidden;
  }
  .scrollable-tab table thead tr th{
    position: inherit;
  }
}
</style>
