<template>
  <div :class="{popup:true, active:visible}" @click="close">
    <div class="popup-body" @click.prevent.stop>
      <center><h2>Ventes</h2></center>
      <div v-if="ventes.length==0">{{action}}</div>
      <div class="scrollable-tab" v-else>
        <table class="table commandes">
          <thead>
            <tr class="panier-item">
              <th>id</th>
              <th>produit</th>
              <th>quantite</th>
              <th>prix achat</th>
              <th>prix vente</th>
              <th>total</th>
            </tr>
          </thead>
          <tbody id="commandes">
              <tr v-for="vente in ventes">
                <td style="color:#aaa;">{{vente.id}}</td>
                <td>{{vente.produit}}</td>
                <td>x {{vente.quantite}}</td>
                <td>{{money(vente.prix_achat)}} Fbu</td>
                <td>{{money(vente.prix_vente)}} Fbu</td>
                <td>{{money(vente.prix_vente*vente.quantite)}} Fbu</td>
              </tr>
          </tbody>
          <tfoot>
            <tr class="panier-item">
              <th colspan="5">total</th>
              <th>{{money(commande.a_payer)}} Fbu</th>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data(){
    return {
      ventes:[],
      action:"récuperation des ventes..."
    }
  },
  props: {
    visible:{ type:Boolean, default:false},
    commande:{ type:Object, required:true}
  },
  watch:{
    visible(is_visible){
      if(this.visible){
        let headers = {
          headers: {
            "Authorization": "Bearer " + this.$store.state.user.access
          }
        }
        axios.get(this.$store.state.url+'/vente/?commande='+this.commande.id, headers)
        .then((response) => {
          this.ventes = response.data;
          this.action = "pas de details."
        }).catch((error) => {
          console.error(error);
        });
      }
    }
  },
  methods: {
    close(){
      this.$emit("close")
    }
  }
};
</script>
