<template>
  <div :class="{popup:true, active:visible}" @click="close">
    <div class="popup-body" @click.prevent.stop @keydown.stop>
      <center>
        <h3>Paiement</h3>
      </center>
      <form method="post">
        <div>
          La somme à payer <b>{{money(cart.getTotal()) }}</b>Fbu
        </div>
        <div class="field">
          <label for="id_somme">La somme payée</label>
          <input type="number" id="id_somme" v-model="payee">
        </div>
        <div>
          La somme à lui retourner <b>{{money(payee - cart.getTotal()) }}</b>Fbu
        </div>
        <hr>
        <div class="field">
          <label for="id_nom">Nom du client</label>
          <input type="text" list="noms" v-model="client.nom"
            id="id_nom" @change="setTel">
        </div>
        <div class="field">
          <label for="id_tel">Téléphone</label>
          <input type="number" v-model="client.tel" id="id_tel"
             list="tels" @change="setNom">
        </div>
        <div class="buttons">
          <div class="logs">{{logs}}</div>
          <input type="submit" value="Vendre" @click="postCommande">
        </div>
      </form>
      <datalist id="noms">
        <option v-for="c in clients" :value="c.nom"/>
      </datalist>

      <datalist id="tels">
        <option v-for="c in clients" :value="c.tel"/>
      </datalist>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  props: {
    visible:{ type:Boolean, default:false},
  },
  data(){
    return {
      client:{ nom:"", tel:""},
      cart: this.$store.state.cart,
      dette_p:null, logs:""
    }
  },
  computed:{
    payee:{
      get(){
        return this.dette_p == null ? this.cart.getTotal() : this.dette_p
      },
      set(val){
        this.dette_p = val
      }
    },
    clients(){
      return this.$store.state.clients
    },
    host(){
      return this.$store.state.url
    },
    username(){
      return this.$store.state.user.username
    },
    headers(){
      return {
        headers: {
          "Authorization": "Bearer " + this.$store.state.user.access
        }
      }
    }
  },
  mounted(){
    if(this.clients.length<1){
      axios.get(this.$store.state.url+'/client/', this.headers)
      .then((response) => {
        this.$store.state.clients = response.data;
      }).catch((error) => {
      });
    }
  },
  methods: {
    close(){
      this.$emit("close")
    },
    setTel(){
      for(let client of this.clients){
        if (client.nom == this.client.nom) {
          this.client.tel = client.tel;
          return;
        }
      }
    },
    setNom(){
      for(let client of this.clients){
        if (client.tel == this.client.tel) {
          this.client.nom = client.nom;
          return;
        }
      }
    },
    postCommande(){
      if(this.payee < this.cart.getTotal()){
        if(this.client.tel.length<7){
          this.logs = "le numero est obligatoire pour les dettes"
          return;
        }
      }
      let data = {};
      let items = [];
      let a_payer = this.cart.getTotal()
      let paid = this.payee<=a_payer?this.payee:a_payer;
      let validated = this.payee == this.cart.getTotal();

      for(let item of this.cart.content){
        items.unshift({"id":item.product.id, "quantite":item.quantite})
      }
      data = {
        "items":items, "client":this.client, "payee":paid,
        "validated":validated, "a_payer":a_payer
      };
      axios.post(this.$store.state.url+"/commande/", data, this.headers)
      .then((response) => {
        let commande = response.data;
        let html = this.templateFacture(this.cart.content, commande.id);
        let produits = this.$store.state.produits
        this.client = { nom:"", tel:""};
        this.$store.state.cart.content = [];
        this.$store.state.commandes.unshift(commande);
        let produit = undefined;
        for(let item of items){
          produit = produits.find(x => x.id == item.id)
          if(!!produit){
            produit.quantite -= item.quantite;
          }
        }
        if(commande.reste>0){
          this.$store.state.dettes.unshift(commande);
        }
        this.$emit("close");
        this.printDiv(html);
      }).catch((error) => {
        if (!!error.response) {
          this.logs = error.response.data.status
        } else {
          this.logs = "une erreur est survenue";
          console.error(error);
        }
      });
    },
    printDiv(html) {
      var a = window.open('', '', 'height=500, width=1000'); 
      a.document.write(html); 
      a.document.close();
      setTimeout(function(){
        a.print();
        a.close();
      }, 1000)
    },
    templateFacture(content, id){
      let facture_body = "";
      for(let item of content){
        facture_body += `
          <tr style="text-align: left;">
            <td>${item.product.nom}</td>
            <td>${item.product.prix_vente} Fbu</td>
            <td>x ${item.quantite}</td>
            <td style="text-align: right;">${item.getTotal()} Fbu</td>
          </tr>
        `
      }
      let facture=`
      <table style="width:100%;">
        <tr>
          <td>
            Facture no. ${id} du ${new Intl.DateTimeFormat('en-GB', { dateStyle: 'short', timeStyle: 'short' }).format(new Date())}<br>
          </td>
        </tr>
        <tr style="display: inline;">
          <td>
            <img width=100px src="/static/img/bimbo.png"/>
          </td>
          <td>
            RC 23606/20<br/>
            NIF 4001409707<br/>
          </td>
        </tr>
        <tr>
          <td>
            Tel: 61 977 004 / 71 369 548<br/>
            BIMBO General Trading (BGT)<br/>
            Bujumbura, Gihosha<br/><br/>
          </td>
        </tr>
        <tr>
          <td><b>${this.client.nom}: ${this.client.tel}</b></td>
        </tr>
        <tr>
        <td>
      </table>
        <table style="width:100%;">
          <tbody>
            <tr style="border-bottom: 1px solid #aaa;text-align: left;">
              <th>Article</th>
              <th style="">P.U.</th>
              <th>Qt.</th>
              <th style="text-align: right;">Total</th>
            </tr>
            ${facture_body}
            <tr style="border-top: 1px solid #aaa;text-align: left;">
              <th colspan="3">Total</th>
              <th style="text-align: right;"><b>${this.cart.getTotal()} Fbu</b></th>
            </tr>
          </tbody>
        </table>
        </td>
        </tr>
        <tr>
        <td>Caissier ${this.username}</td>
        </tr>
        <tr>
        <br>
        <td style="text-align: center;"><strong>Murakoze, Merci, Thank you!</strong></td>
        </tr>
        `;
      return(facture);
    }
  }
};
</script>
<style scoped>
form{
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}
form div{
  margin: 5px;
}
.buttons{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.buttons *{
  margin: 0;
}
.logs{
  width: 180px;
  color: red;
}
</style>