<template>
  <div class="parent" @click="popover_opened=false">
    <div class="top">
      <SearchBar :show_date="false" @searched="searchItem"/>
    </div>
    <div class="recycler">
    </div>
    <div class="scrollable-tab">
      <table class="table pertes">
        <thead>
          <tr class="panier-item">
            <th>id</th>
            <th>produit</th>
            <th>date</th>
            <th>quantité perdu</th>
            <th>prix d'achat</th>
            <th>motif</th>
            <th></th>
          </tr>
        </thead>
        <tbody id="pertes">
          <tr v-for="perte in pertes" v-if="!perte.validated">
            <td>{{ perte.id }}</td>
            <td>{{ perte.produit }}</td>
            <td>{{ datetime(perte.date) }}</td>
            <td>{{ perte.quantite }}</td>
            <td>{{ money(perte.prix_vente*perte.quantite) }}</td>
            <td>{{ perte.details }}</td>
            <td v-if="userIs('admin')">
              <button @click="acceptPerte(perte)">Accepter</button>
              <button @click="deletePerte(perte)">Refuser</button>
            </td>
          </tr>
        </tbody>
        <tfoot>
          <tr class="panier-item">
            <th colspan="4">total</th>
            <th>{{money(total)}} Fbu</th>
            <th>
              <button @click="perte_shown=true">ajouter</button>
            </th>
            <th v-if="userIs('admin')">
                <button>Tout accepter</button>
                <button>Tout refuser</button>
            </th>
          </tr>
        </tfoot>
      </table>
    </div>
    <PerteDialog :visible.sync="perte_shown"
      @close="perte_shown=false" @added="pushPerte"/>
  </div>
</template>

<script>
import axios from "axios";
import SearchBar from "../components/searchbar";
import PerteDialog from "../components/dialog_perte";

export default {
  components:{
    SearchBar, PerteDialog,
  },
  data(){
    return{
      pertes : [], perte_shown:false,
    }
  },
  computed:{
    total(){
      let tot = 0;
      if (this.pertes.length == 0 ) return tot;
      for(let perte of this.pertes) {
        if(!perte.validated)
          tot += perte.prix_vente*perte.quantite;
      }
      return tot;
    },
    headers(){
      return {
        headers: {
          "Authorization": "Bearer " + this.$store.state.user.access
        }
      }
    }
  },
  methods:{
    userIs(group){
      return this.$store.state.user.groups.includes(group);
    },
    searchItem(value){
      let returnedValues = this.$store.state.pertes.filter(item => {
        return item.id == value | item.produit.toLowerCase().includes(value.toLowerCase())
      })
      this.pertes = returnedValues;
    },
    pushPerte(data){
      this.pertes.push(data);
    },
    acceptPerte(perte){
      if(confirm("Acceptez-vous vraiment cette perte?")){
        console.log("triggered")
        let data = {"validated":true}
        axios.patch(this.$store.state.url+`/perte/${perte.id}/`, data, this.headers
        ).then((response) => {
          perte.validated = true;
        }).catch((error) => {
          console.error(error);
        })
      }
    },
    deletePerte(perte){
      if(confirm(`Etes vous sur de supprimer cette perte?
NB: Si vous supprimez cette perte, sa quantité sera réajoutée dans le stock`)){
        let data = {"validated":true}
        axios.delete(this.$store.state.url+`/perte/${perte.id}/`, this.headers
        ).then((response) => {
          this.pertes.splice(this.pertes.indexOf(perte), 1);
          this.$store.state.produits.find(
            x => x.id==perte.produit_id
          ).quantite += perte.quantite;
        }).catch((error) => {
          console.error(error);
        })
      }
    },
  },
  mounted(){
    axios.get(this.$store.state.url+'/perte/', this.headers)
    .then((response) => {
      this.$store.state.pertes = response.data;
      this.pertes = response.data;
    }).catch((error) => {
      console.error(error)
    });
  }
};
</script>
<style scoped>
.top{
  display: flex;
  justify-content: center;
}
.scrollable-tab{
  width: 100%;
  /*height: 0px;*/
  overflow: auto;
  padding: 0 20px;
  position: relative;
}
.scrollable-tab table thead tr th{
  position: sticky;
  top: 0;
}
.scrollable-tab table tfoot tr th{
  position: sticky;
  bottom: 0;
}
@media screen and (max-width: 650px){
  .scrollable-tab{
    position: inherit;
    height: auto;
    overflow-y: hidden;
  }
  .scrollable-tab table thead tr th{
    position: inherit;
  }
}
</style>
