<template>
  <div class="parent" @click="popover_opened=false">
    <div class="top">
      <SearchBar :show_date="false" @searched="searchItem"/>
    </div>
    <div class="recycler">
    </div>
      <div class="scrollable-tab">
        <table class="table paiements">
          <thead>
            <tr class="panier-item">
              <th>id</th>
              <th>commande</th>
              <th>Date</th>
              <th>somme</th>
              <th>options</th>
            </tr>
          </thead>
          <tbody id="paiements">
            <tr v-for="paiement in paiements">
              <td>{{paiement.id}}</td>
              <td>{{paiement.commande}}</td>
              <td>{{datetime(paiement.date)}}</td>
              <td>{{money(paiement.somme)}} Fbu</td>
              <td>
                <div class="btns">
                  <button @click="validatePay(paiement)" v-if="user.is_admin">valider</button>
                  <button @click="deletePay(paiement)">annuler</button>
                </div>
              </td>
            </tr>
        </tbody>
        <tfoot>
          <tr class="panier-item">
            <th>total</th>
            <th></th>
            <th></th>
            <th>{{money(total)}} Fbu</th>
            <th><button v-if="user.is_admin">
              valider tout
            </button></th>
          </tr>
        </tfoot>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import SearchBar from "../components/searchbar";

export default {
  components:{ SearchBar},
  data(){
    return{
      paiements : this.$store.state.paiements,
      rembours_opened:false,
      active_paiement :{},
      query_date : false
    }
  },
  computed:{
    total(){
      let tots =  0;
      if (this.paiements.length == 0 ) return tots;
      for(let paiement of this.paiements) {
        tots += paiement.somme;
      }
      return tots;
    },
    user(){
      return this.$store.state.user;
    },
    headers(){
      return {
        headers: {
          "Authorization": "Bearer " + this.$store.state.user.access
        }
      }
    }
  },
  methods:{
    searchItem(value){
      let returnedValues = {};
      if (value=='') 
        return this.fetchPayements();
      !this.query_date && value!= ''? 
        returnedValues = this.$store.state.paiements.filter(item => {
          return item.id == value | item.commande.toLowerCase().includes(value.toLowerCase())
        }) :
        returnedValues = this.paiements.filter(item => {
          return item.id == value | item.commande.toLowerCase().includes(value.toLowerCase())
        })
      this.paiements = returnedValues;
    },
    rembourser(paiement){
      this.active_paiement = paiement;
      this.rembours_opened = true ;
    },
    deletePay(paiement){
      if(confirm("êtes-vous sur de vouloir annuler la paiement?")){
        axios.delete(this.$store.state.url+`/paiement/${paiement.id}/`, this.headers)
        .then((response) => {
          let index = this.$store.state.paiements.indexOf(paiement);
          let commande = this.$store.state.commandes.find(x => x.id == paiement.commande_id);
          let dette = this.$store.state.dettes.find(x => x.id == paiement.commande_id);
          if(!!commande){
            commande.uncommited-=paiement.somme;
          }
          if(!!dette){
            dette.uncommited-=paiement.somme;
          }
          this.$store.state.paiements.splice(index, 1);
        }).catch((error) => {
          console.error(error);
        });
      }
    },
    validatePay(paiement){
      if(confirm("avez-vous vraiment recu l'argent liquide?")){
        let data = {"validated":true}
        axios.patch(this.$store.state.url+`/paiement/${paiement.id}/`, data, this.headers)
        .then((response) => {
          let index = this.$store.state.paiements.indexOf(paiement);
          let commande = this.$store.state.commandes.find(x => x.id == paiement.commande_id);
          let dette = this.$store.state.dettes.find(x => x.id == paiement.commande_id);
          if(!!commande){
            commande.payee+=paiement.somme;
            commande.uncommited-=paiement.somme;
          }
          if(!!dette){
            dette.reste-=paiement.somme;
            if(dette.reste <= 0){
              let i = this.$store.state.dettes.indexOf(dette);
              this.$store.state.dettes.splice(i, 1);
            }else{
              dette.payee+=paiement.somme;
              dette.uncommited-=paiement.somme;
            }
          }
          this.$store.state.paiements.splice(index, 1);
        }).catch((error) => {
          console.error(error);
        });
      }
    },
    validateAllPay(){
    },
    fetchPayements(){
      axios.get(this.$store.state.url+'/paiement/', this.headers)
      .then((response) => {
        this.$store.state.paiements = response.data;
        this.paiements = response.data;
      }).catch((error) => {
        if(error.response.data.code == "token_not_valid"){
          let data = {
            refresh: this.$store.state.user.refresh
          };
          axios.post(this.$store.state.url+'/refresh/', data)
          .then((response) => {
            this.$store.state.user.access = response.data.access;
            headers.headers.Authorization = "Bearer " + response.data.access
          }).catch((error) =>{
            console.error(error);
          })
        }
      });
    }
  },
  mounted(){
    if (this.paiements.length==0) {
        this.fetchPayements();
    }
  }
};
</script>
