<template>
  <div class="parent" @click="popover_opened=false">
    <div class="top">
      <SearchBar @changed="filterByDate" @searched="searchItem" :show_date="userIs('admin')||userIs('gerant')"/>
    </div>
    <div class="recycler">
    </div>
      <div class="scrollable-tab">
        <table class="table commandes">
          <thead>
            <tr class="panier-item">
              <th>id</th>
              <th>caissier</th>
              <th>Date</th>
              <th>somme</th>
              <th>payée</th>
              <th>Reste</th>
              <th>options</th>
            </tr>
          </thead>
          <tbody id="commandes">
            <tr v-for="commande in commandes"
              :class="{'uncommited':commande.uncommited>0}">
              <td>{{commande.id}}</td>
              <td>{{commande.user}}</td>
              <td>{{datetime(commande.date)}}</td>
              <td>{{money(commande.a_payer)}} Fbu</td>
              <td>{{money(commande.payee)}} Fbu</td>
              <td>{{money(commande.reste - commande.uncommited)}} Fbu</td>
              <td>
                <div class="btns">
                  <button @click="showDetails(commande)">
                    détails
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
          <tfoot>
            <tr class="panier-item">
              <th>total</th>
              <th></th>
              <th></th>
              <th>{{money(totals.a_payer)}} Fbu</th>
              <th>{{money(totals.payee)}} Fbu</th>
              <th>{{money(totals.reste)}} Fbu</th>
              <th></th>
            </tr>
          </tfoot>
        </table>
      </div>
     <DetailsDialog :visible='details_opened' :commande='active_commande' @close="details_opened=false"/>
  </div>
</template>

<script>
import axios from "axios";
import SearchBar from "../components/searchbar";
import DetailsDialog from "../components/dialog_ventes";

export default {
  components:{
    SearchBar, DetailsDialog
  },
  data(){
    return{
      query_date : false,
      commandes : this.$store.state.commandes,
      details_opened:false,
      active_commande :{},
    }
  },
  computed:{
    totals(){
      let tots = { a_payer: 0, payee: 0, reste: 0}
      if (this.commandes.length == 0 ) return tots;
      for(let commande of this.commandes) {
        tots.a_payer += commande.a_payer;
        tots.payee += commande.payee;
        tots.reste += commande.reste;
      }
      return tots;
    },
  },
  methods:{
    userIs(group){
      return this.$store.state.user.groups.includes(group);
    },
    searchItem(value){
      let returnedValues = {};
      if (value=='') return 
      value = value.toLowerCase();
      value = value.toLowerCase().split(",")
      returnedValues = this.commandes.filter(item => {
        if(!value[1]){
          return(
            item.id == value[0] || 
            item.user.toLowerCase().includes(value[0]) || 
            this.datetime(item.date).includes(value[0])
          )
        } else {
          return (
            (
              item.id == value[0] || 
              item.user.toLowerCase().includes(value[0]) || 
              this.datetime(item.date).includes(value[0])
            ) && 
            (
              item.id == value[1] || 
              item.user.toLowerCase().includes(value[1]) || 
              this.datetime(item.date).includes(value[1])
            )
          )
        }
      })
      this.$store.state.commandes_fetched = false
      this.commandes = returnedValues;
    },
    showDetails(commande){
      this.active_commande = commande;
      this.details_opened=true;
    },
    fetchData(){
      let headers = {
          headers: {
            "Authorization": "Bearer " + this.$store.state.user.access
          }
        }
      axios.get(this.$store.state.url+'/commande/', headers)
      .then((response) => {
        this.$store.state.commandes = response.data;
        this.commandes = response.data;
        this.$store.state.commandes_fetched = true;
      }).catch((error) => {
        if(error.response.data.code == "token_not_valid"){
          let data = {
            refresh: this.$store.state.user.refresh
          };
          axios.post(this.$store.state.url+'/refresh/', data)
          .then((response) => {
            this.$store.state.user.access = response.data.access;
            this.fetchData()
          }).catch((error) =>{
            this.$store.state.user = null;
          })
        }
      });
    },
    filterByDate(e){
      let since = e.du;
      let to = e.au;

      let headers = {
        headers: {
          "Authorization": "Bearer " + this.$store.state.user.access
        }
      }
      axios.get(this.$store.state.url+`/commande/?du=${since}&au=${to}`, headers)
      .then((response) => {
        this.query_date=true;
        this.commandes = response.data;
      }).catch((error) => {
        this.query_date = false;
        console.log(error);
      });
      
    }
  },
  mounted(){
    if(!this.$store.state.commandes_fetched){
      this.fetchData()
    }
  }
};
</script>
<style scoped>
.top{
  display: flex;
  justify-content: center;
}
.scrollable-tab{
  width: 100%;
  /*height: 0px;*/
  overflow: auto;
  padding: 0 20px;
  position: relative;
}
.uncommited{
  color: red;
}
.scrollable-tab table thead tr th{
  position: sticky;
  top: 0;
}
.scrollable-tab table tfoot tr th{
  position: sticky;
  bottom: 0;
}
@media screen and (max-width: 650px){
  .scrollable-tab{
    position: inherit;
    height: auto;
    overflow-y: hidden;
  }
  .scrollable-tab table thead tr th{
    position: inherit;
  }
}
</style>
