<template>
  <div :class="{popup:true, active:visible}" @click="close">
    <div class="popup-body" @click.prevent.stop @keydown.stop>
      <center>
        <h3>Perte</h3>
      </center>
      <form method="post">
        <div class="field">
          <label for="id_nom">Nom du produit</label>
          <input type="text" list="noms" v-model="nom_produit">
        </div>
        <div class="field">
          <label for="id_quantite">Quantité</label>
          <input type="number" v-model="quantite">
        </div>
        <div class="field">
          <label for="id_tel">Détails</label>
          <textarea v-model="details" list="tels">
          </textarea>
        </div>
        <div class="buttons">
          <div class="logs">{{logs}}</div>
          <input type="submit" value="Soumettre" @click="postPerte">
        </div>
      </form>
      <datalist id="noms">
        <option v-for="p in produits" :value="p.nom"/>
      </datalist>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  props: {
    visible:{ type:Boolean, default:false},
  },
  data(){
    return {
      logs:"", nom_produit:"", quantite:0, details:""
    }
  },
  computed:{
    produits(){
      return this.$store.state.produits
    }
  },
  methods: {
    close(){
    	this.$emit("close")
    },
    postPerte(){
      let headers = {
        headers: {
          "Authorization": "Bearer " + this.$store.state.user.access
        }
      }
      let produit = this.$store.state.produits.find(x => x.nom == this.nom_produit);
      let data = {
        "produit":produit.id, "quantite":this.quantite,
        "details":this.details
      }
      if(data.quantite<=0){
        this.logs = "cette quantité est invalide"
        return;
      }
      if(data.details.length<7){
        this.logs = "vous devez donner un justification valide"
        return;
      }
      axios.post(this.$store.state.url+"/perte/", data, headers)
      .then((response) => {
        this.$emit("added", response.data)
        produit.quantite -= response.data.quantite;
        this.close();
      }).catch((error) => {
        if (!!error.response) {
          this.logs = error.response.data.status
        } else {
          this.logs = "une erreur est survenue";
          console.error(error);
        }
      });
    },
  }
};
</script>
<style scoped>
form{
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}
form div{
  margin: 5px;
}
.buttons{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.buttons *{
  margin: 0;
}
.logs{
  width: 180px;
  color: red;
}
</style>